.container{
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
	align-items: center;
	position: relative;
	background-color: #0C0C0C;
}
.svgTop {
	position: absolute;
	bottom: 99%;
	left: 0;
	width: 100%;
	z-index: 3;
	fill: #0C0C0C;
}
.svgFooter{
	position: absolute;
	top: 97%;
	left: 0;
	width: 100%;
	z-index: 3;
	fill: #0C0C0C;
	overflow: hidden;
}
@media (max-width: 1124px) {
	.container {flex-direction: column-reverse}
}
@media (max-width: 600px) {
	.svgFooter {display: none}
}